html {
  scroll-behavior: smooth;
  -webkit-scroll-behavior: smooth;
  -moz-osx-scroll-behavior: smooth;

}

body {
  margin: 0;
  background-color: rgb(158 158 158 / 6%);
  font-family: "Quicksand", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #21333b;
}


.courses {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 40px;
}
ul {
  list-style: none;
  margin-top: 40px;
}

li {
  margin-bottom: 10px;
}
.courses {
  font-size: 1.1rem;
  line-height: 2rem;
}

.year {
  font-weight: 700;
  margin-right: 10px;
  color:#311368;
}

.school {
  margin-left: 111px;
  font-weight: 200;
  font-style: italic;
  color: #311368;
}

.content_icon {
  color: #311368;
}

h1 {
  font-size: 2rem;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .courses {
    margin-top: 0px;
    font-size: 0.9rem;
    line-height: 1.5rem;
  }

  .courses > .content > ul {
    padding-left: 5vw !important;
  }

  h1 {
    font-size: 1.4rem;
    margin-bottom: 0;
  }
  .school {
    margin-left: 0;
  }
}

.card {
  width: 440px;
  height: 520px;
  margin-bottom: 40px;
  border-radius: 10% !important;
  transition: transform 450ms ease-in-out !important;
}

.MuiButton-contained {
  color: white !important;
  background-color: #311368 !important;
  margin-bottom: 5% !important;
  border-top-right-radius: 35% !important;
  border-bottom-left-radius: 35% !important;
  padding: 2.5% !important;
  font-family: "Quicksand", sans-serif !important;
}

.MuiButton-contained:hover {
  background-color: #503385 !important;
}

p.MuiTypography-root {
  color: #21333b !important;
  line-height: 1.4rem !important;
  font-family: "Quicksand", sans-serif;
  font-size: 1.15em !important;
  border-radius: 10% !important;
}

ul.tech_list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-left: 0;
  margin-bottom: 0;
}

.tech_list > li {
  margin-right: 10px;
  font-size: 1.1em;
}

.MuiCardActions-root {
  justify-content: center !important;
}

ul.tech_list {
  margin-top: 0;
  color: #21333b;
  font-family: "Quicksand", sans-serif;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .card {
    width: 90vw;
    height: auto;
    border-radius: 5% !important;
  }
}

.projects > .content {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin-top: 80px;
}

.projects > h1 {
  text-align: center;
  font-size: 2rem;
}

.projects > .content.hidden {
  display: none;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .projects > .content {
    margin-top: 30px;
  }

  .projects > h1 {
    font-size: 1.4rem;
  }
}

.home {
  margin-top: 85px;
  color: white;
  border-radius: 0% 0% 40% 40%/20% 20% 40% 40%;
  background: #6441a5; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #2a0845,
    #6441a5
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.home > .content {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.text {
  margin-top: 15vh;
  margin-bottom: 15vh;
  width: 60vw;
  text-align: center;
  font-size: 1.2rem;
  line-height: 1.8rem;
}

.text > h2 {
  width: 420px;
  height: 100px;
  margin-bottom: 50px;
  line-height: 1.7em !important;
  margin-left: auto;
  margin-right: auto;
  padding: 0 !important;
  font-weight: lighter;
  font-size: 1.5em !important;
}

.text > p {
  text-align: left;
  margin-top: 80px;
  font-size: 1.1rem;
  line-height: 2rem;
  width: 50vw;
  margin-left: auto;
  margin-right: auto;
}

.content > img {
  margin-top: 5vh;
  object-fit: contain;
  width: 20vw;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .home {
    border-radius: 0% 0% 40% 40%/20% 20% 20% 20%;
  }
  .content > img {
    margin-top: 10px;
    width: 40vw;
  }
  .text {
    margin-top: 5vh;
    margin-bottom: 5vh;
    width: 90vw;
  }
  
  .text > p {
    font-size: 0.9rem;
    text-align: justify;
    margin-top: 30px;
    padding-bottom: 25px;
    width: 80vw;
    text-align: left;
  }

  .text > h2 {
    width: 75vw;
    height: 50px;
    font-size: 1.2rem !important;
    line-height: 2rem !important;
    margin-bottom: 80px !important;
  }
}

.header {
  display: flex;
  align-items: center;
  height: 100px;
  width: 100vw;
  color: white;
  position: fixed;
  top: 0;
  z-index: 1;
  transition: all 450ms ease-in-out;
  background-image: inherit;
}

.header > .menu_icon {
  position: fixed;
  top: 25px;
  right: 25px;
  font-size: 3rem;
  cursor: pointer;
  transition: transform 450ms ease-in-out;
}

.header > .menu_icon:hover {
  transform: scale(1.08);
}

.header > .my_logo {
  margin-left: 20px;
  width: 120px;
  height: auto;
}

.up_arrow {
  position: fixed !important;
  bottom: 10px !important;
  right: 10px !important;
  z-index: 1 !important;
  background-color: #311368 !important;
  color: white !important;
  opacity: 0.9 !important;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .header > .my_logo {
    width: 80px;
    height: auto;
  }

  .header > .menu_icon {
    font-size: 2.5rem;
    top: 30px;
  }

  .up_arrow {
    position: fixed !important;
    bottom: 80px !important;
    right: 5px !important;
    opacity: 0.9 !important;
  }
}

.navbar {
  height: 100%;
  width: 320px;
  display: flex;
  flex-direction: column;
  background-color: #e0e0e0;
  color: #111;
  z-index: 2;
  position: fixed;
  top: 0;
  right: 0;
  transition: all 450ms ease-out;
}

.navbar.hidden {
  margin-right: -320px;
}

.navbar > div {
  display: flex;
  align-items: center;
  padding-left: 50px;
  padding-top: 2vh;
  padding-bottom: 2vh;
  margin-top: 1vh;
  cursor: pointer;
  transition: all 0.4s ease-in-out;
}

.navbar > div:hover {
  background-color: #ffffff87;
  padding-left: 60px;
}

.navbar > div > a {
  width: 100%;
  text-decoration: none;
  color: #21333b;
  font-size: 1.1rem;
  font-weight: bold;
  padding-left: 10px;
}
.navbar_icon {
  color: #21333b;
  transition: all 0.45s ease-in-out;
  font-size: 1.5rem !important;
}

.navbar_icon:hover {
  transform: scale(1.08);
}

.close_button {
  color: #21333b;
  cursor: pointer;
  align-self: flex-end;
  position: relative;
  top: 0;
  right: 25px;
  margin-bottom: 10vh;
}

.close_button:hover {
  background-color: #e0e0e0 !important;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .navbar {
    width: 300px;
  }

  .navbar > div > a {
    font-size: 1rem;
  }

  .close_button {
    top: 15px;
  }
}

.websiteLang {
  color: white;
  font-size: 1.2em;
  font-weight: bold;
  position: fixed;
  top: 35px;
  right: 140px;
}

.websiteLang > .lang {
  padding-right: 6px;
  padding-left: 6px;
  cursor: pointer;
}

.websiteLang > .lang:hover {
  color: #ffeb3b;
}

.websiteLang > .active {
  color: #ffeb3b;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .websiteLang {
    font-size: 0.85em;
    top: 40px;
    right: 100px;
  }

  .websiteLang > .lang {
    padding-right: 8px;
    padding-left: 8px;
  }
}

.skills {
  text-align: center;
  margin-top: 60px;
}

.skills > .content {
  margin-top: 80px;
}

.logo.hidden {
  display: none;
}

.logos {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.logo > img {
  width: 100px;
  height: 100px;
  object-fit: contain;
  transition: transform 450ms ease-in-out;
  cursor: pointer;
}

.logo > img:hover {
  transform: scale(1.08);
}

.logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 100%;
  margin-left: 40px;
  margin-right: 40px;
  margin-bottom: 40px;
  flex-basis: 19%;
}

.progress {
  transition: transform 450ms linear;
  color: #311368;
}

.progress:hover {
  transform: scale(1.08);
}

.MuiCircularProgress-static {
  color: #311368 !important;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .skills {
    margin-top: 0;
  }
  .skills > .content {
    margin-top: 30px;
  }

  .logo > img {
    width: 70px;
    height: 70px;
  }

  .logo {
    margin-left: 20px;
    margin-right: 10px;
    margin-bottom: 40px;
  }
}

.hobbies {
  text-align: center;
  margin-top: 60px;
}

.hobbies > .content {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  margin-top: 80px;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .hobbies {
    margin-top: 0;
  }
  .hobbies > .content {
    margin-top: 30px;
  }
}

.hobby > img {
  width: 200px;
  height: 200px;
  object-fit: cover;
  border-radius: 100%;
  transition: all 450ms ease-in-out;
}

.hobby > img:hover {
  transform: scale(1.08);
  cursor: pointer;
}

.hobby > .hobby_desc {
  font-size: 1rem;
  line-height: 1.6rem;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .hobby {
    margin-bottom: 20px;
  }
  .hobby > img {
    width: 120px;
    height: 120px;
  }
  .hobby > h4 {
    font-size: 0.9rem !important;
  }
  .hobby > .hobby_desc {
    font-size: 0.9rem;
    line-height: 1.6rem;
  }
}

.contact {
  background: #6441a5; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #2a0845,
    #6441a5
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  border-radius: 60% 60% 0% 0%/100% 100% 100% 100%;
  color: white;
  height: 100px;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: center;
  margin-top: 80px;
}

.contact > h4 {
  font-weight: 300;
  font-size: 1rem;
  margin: 0;
}
.contact > .icon_list {
  color: white;
}

.contact_icon {
  color: white;
  transition: all 450ms ease-in-out !important;
}

.contact_icon:hover {
  transform: scale(1.08);
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .contact {
    margin-top: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100px;
    border-radius: 0;
  }

  .contact_icon {
    padding-left: 15px;
    padding-right: 15px;
  }
}

